.login {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
}

.login__icon {
  width: 89px;
  height: 86px;
  margin: 0 0 35px 0;
}

.login__title {
  display: inline-block;
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
}