input {
  width: 100%;
  min-width: 260px;
  max-width: 300px;
}

input[type=text] {
  height: 60px;
  background: #FFFFFF;
  border-radius: 20px;
  border: 0;
  text-align: center;
  outline:none;
}

input[type=tel] {
  height: 60px;
  background: #FFFFFF;
  border-radius: 20px;
  border: 0;
  text-align: center;
  outline:none;
}

input[type=tel]:focus {
  border: 0;
}

input[type=password] {
  height: 60px;
  background: #FFFFFF;
  border-radius: 20px;
  border: 0;
  text-align: center;
  outline:none;
}

input, select, textarea{
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #7C939C;
}