.button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-width: 260px;
  max-width: 300px;
  height: 60px;
  border-radius: 20px;
  text-decoration: none;
  border: none;
}

.button__title {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #3D5054;
}

.primary .button__title, .secondary .button__title {
  color: #FFFFFF;
}

.primary {
  background: radial-gradient(100% 100% at 50% 0%, #FFC700 0%, #F89500 100%);

}

.secondary {
  background: radial-gradient(100% 100% at 50% 0%, #3D5054 0%, #263337 100%);
}